import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class WeatherService {
    private result = {};
    private cityName: string;
    private degree: string;

    constructor(private http: Http){
        
    }
    getWeather(city){
        return this.http.get('http://api.openweathermap.org/data/2.5/weather?appid=1732bea0d0df49b4d41d173bf004a9d5&units=metric&q=' + city)
        .toPromise()
        .then(value => {
            //console.log(value.json());
            //this.cityName = value.json().name;
            //console.log(this.cityName);
            return value.json()
        })
        .then(valueJson => {
            this.cityName = valueJson.name;
            //this.result = valueJson.main;
            this.degree = valueJson.main.temp;
            //console.log(this.cityName);
            // console.log(this.degree);

            return this.cityName + " is now " + this.degree;
        });

    }
}


