import { Component } from "@angular/core";

@Component({
    selector: "baihat",
    template: `
        <h3>{{ tenBH }}</h3>
        <h3 [style.color]="'red'">Bai so 3</h3>
        <h3 [style.color]="textColor">Bai so 4</h3>
        <div [class.aoxanh]="true">Bai so 5</div>
        <img 
            (click)="clickHinh($event)"
        src="{{hinh}}" [attr.logo]="mauchu" />
    `,
    styles: [
        `
        .aoxanh{
            color: blue;
            background-color: yellow
        }
        `
    ]
})

export class BaihatComponent {
    tenBH: string = "Demo 10";
    textColor: string = "blue";
    hinh: string = "https://i-dulich.vnecdn.net/2018/01/07/1_360x216.jpg";
    mauchu: string = "blue";

    constructor(){
        this.tenBH = "Phia sau mot co gai";
        this.TaoBH();
    }

    clickHinh(e){
        console.log(e);
        alert(e.clientX);
        
    }

    TaoBH(){
        this.tenBH = "Hello world";
    }
}


