import { Component } from "@angular/core";

@Component({
    selector: "cong-tru",
    template: `
        <a href="#">Click here</a>
        {{ value }}<br/>
        <input class="btn btn-danger" type="button" value="+" (click)="clickCong()">
        <input class="btn btn-primary" type="button" value="-" (click)="clickTru()">
    `
})

export class CongTruComponent{
    value: number = 0;

    clickCong(){
        this.value++;
    }
    clickTru(){
        this.value--;
    }

    mouseIn(e){
        // console.log("123");
        // console.log(e);
        // e.srcElement.src = this.img2;
    }
    mouseOut(e){
        // e.srcElement.src = this.img1;
        // console.log("456");
    }
}


