import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-book',
  templateUrl: './book.component.html',
  styleUrls: ['./book.component.css']
})
export class BookComponent implements OnInit {
  forgot: boolean = true;
  en: string = "Hello";
  name: string = "";

  constructor() { }

  ngOnInit() {
  }

  toggleForgot(){
    this.forgot = !this.forgot;
  }

}



